import React, {useState, useEffect} from 'react';
import ReactDOM from 'react-dom';
import phonebook from './services/phonebook'
import Form from './components/Form'
import FilterBar from './components/FilterBar'
import People from './components/People'
import Notification from './components/Notification'


const App = () => {
    const [persons, setPersons] = useState([])
    const [newName, setNewName] = useState('')
    const [newPhone, setNewPhone] = useState('')
    const [filter, setNewFilter] = useState('')
    const [notificationMessage, setNotificationMessage] = useState('')

    useEffect(() => {
        phonebook
            .getAll()
            .then(response => {
                console.log(response.data)
                setPersons(response.data)
            })
    }, [])

    const handleChange = (e) => {
        setNewName(e.target.value)
    }

    const handlePhoneChange = (e) => {
        setNewPhone(e.target.value)
    }

    

    const addPerson = (e) => {
        e.preventDefault()
        const nameToAdd = { name: newName, number: newPhone, id: persons.length + 1 }
        setNewName('')
        setNewPhone('')
        const checkIfExists = persons.find(person => person.name === nameToAdd.name)
        if (checkIfExists === undefined) {
            phonebook
                .addPerson(nameToAdd)
                setPersons(persons.concat(nameToAdd))

            setNotificationMessage(`lisättiin ${nameToAdd.name}`)

            setTimeout(() => {
                setNotificationMessage(null)
            }, 5000)
            
        } else {
            alert(`${nameToAdd.name} on jo luettelossa`)
        }
        
    }

    const handleFilter = (e) => {
        setNewFilter(e.target.value)
    }

    const handleDelete = id => { 
        return () => {
            
            if (window.confirm(`Do you really want to delete ${persons[id-1].name}`)) {
                phonebook.deletePerson(id)
                

                setNotificationMessage(`poistettiin ${persons[id-1].name}`)

                setTimeout(() => {
                    setNotificationMessage(null)
                }, 5000)
                
            }
            
        }
        
    }

    const showPeople = () => {
        if (filter === '') {
            return persons.map(person =>
                <li key={person.id}>{person.name} : {person.number} <button onClick={handleDelete(person.id)}>x</button></li>)
        } 
        return persons.filter(person => 
            person.name
            .toLowerCase()
            .includes(filter.toLowerCase()))
            .map(person =>
                <li key={person.id}>{person.name} : {person.number}<button onClick={handleDelete(person.id)}></button></li>
        )
    }
    
        
    
  
    return (
      <div>
        <h2>Puhelinluettelo</h2>
            <FilterBar handleFilter={handleFilter} />    
        <h2>Add a new thing</h2>
        <Notification message={notificationMessage} />
            <Form 
            onSubmit={addPerson}
            inputName={newName}
            inputNum={newPhone}
            onNumberChange={handlePhoneChange}
            onNameChange={handleChange}
            />
        <h2>Numerot</h2>
            <People showPeople={showPeople()} />
      </div>
    )
  
}

ReactDOM.render(<App />, document.getElementById('root'));

