import React from 'react'

const Form = (props) => {
    return (
        <form onSubmit={props.onSubmit}>
            <div>
            <label>Nimi</label>
            <input value={props.inputName} onChange={props.onNameChange} />
            <br />
            <label>Numero</label>
            <input value={props.inputNum} onChange={props.onNumberChange}/>
            </div>
            <div>
                <button type="submit" >lisää</button>
            </div>
        </form>
    )
}
 





export default Form