import React from 'react'
import { genericTypeAnnotation } from '@babel/types';

const Notification = ({message}) => {
    
    if (message === null) {
        return null
    }

    const addedStyle = {
        borderWidth: '3px',
        borderColor: 'green',
        backgroundColor: 'lightgreen',
        color: 'green'
    }

    const removeStyle = {
        borderWidth: '3px',
        borderColor: 'green',
        backgroundColor: 'lightred',
        color: 'red'    
    }

    if (message.includes('poistettiin')) {
        return (
            <div style ={removeStyle}>
                <h2>{message}</h2>
            </div>
        )
    }
    
    return (
        <div style={addedStyle}>
            <h2>{message}</h2>
        </div>
    )
}

export default Notification